import { ThreeDots } from 'react-loader-spinner';
import { useSelector } from 'react-redux';

const Loader = () => {
  const isLoading = useSelector(state => state.isLoading);
  return (
    isLoading && (
      <ThreeDots
        height="80"
        width="80"
        radius="9"
        color="#4fa94d"
        ariaLabel="three-dots-loading"
        wrapperStyle={{}}
        wrapperClassName=""
        visible={true}
      />
    )
  );
};

export default Loader;
